import React from "react"
import {GatsbyImage} from "gatsby-plugin-image";
import styled from "styled-components"

const ThumbnailCard = styled.div`
    width: 100%;
    height: 200px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 5px;
    padding: 10px;
    transition: transform .5s;
    background: #fff;
    
    p {
        width: fit-content;
        max-width: 100%;
        margin: 0;
        overflow:hidden;
        text-overflow:ellipsis;
        white-space:nowrap;
        font-size: 25px;
        font-weight: 600;
    }
    
    svg {
        width: 80%;
    }
    
    .baekjoon {
        img {
            display: block;
            width: 100%;
        }
    }
    
    .react {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 10px;
        width: 100%;
        height: 100%;
        background: #20232a;
        img {
            width: 20%;
        }
        span {
            color: #61dafb;
            font-size: xx-large;
            font-weight: bold;
        }
    }
`

